@font-face {
    font-family: Georgia; /* Имя шрифта */
    src: url('../../../fonts/9605.ttf'); /* Путь к файлу со шрифтом */
}
@font-face {
    font-family: Arial; /* Имя шрифта */
    src: url('../../../fonts/9041.ttf'); /* Путь к файлу со шрифтом */
}

$georgia: Georgia, serif;
$arial: Arial, sans-serif;


html {
    box-sizing: border-box;
}

.container {
    max-width: 320px;
    padding: 0 10px;
    margin: 0 auto;
}

.main {
    background: url(../../../../assets/fonts/noise_transparent@2x.png), radial-gradient(100% 215.42% at 0% 0%, #5B483A 0%, #262425 100%), #211F20;
    width: 100%;

   
}

.header {
    display: flex;
    padding: 30px 0 30px 0;

    .logo {

        display: flex;
        flex-direction: column;
        align-items: center;

        h1 {
            margin: 0;
            padding: 0;
            font-family: $georgia;
            font-style: normal;
            font-weight: normal;
            font-size: 32px;
            line-height: 110%;
            letter-spacing: 0.06em;
            color: #F1CDB3;
        }
        p {
            margin: 0;
            padding: 10px 0 0 0;
            font-family: Arial;
            font-style: normal;
            font-weight: normal;
            font-size: 13px;
            line-height: 15px;
            letter-spacing: 0.1em;
            color: #FFFFFF;
        }
    }

    img {
        padding: 0 42px;
        align-self: center;
    }

    nav {
        display: none;
    }
}


.not-only {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;

    h2 {
        max-width: 199px;
        font-family: Georgia;
        font-style: normal;
        font-weight: normal;
        font-size: 25px;
        line-height: 130%;
        letter-spacing: 0.06em;
        color: #FFFFFF;
        margin: 0 auto;
        padding: 30px 0 0 0;
    }

    p {
        font-family: Arial;
        font-style: normal;
        font-weight: normal;
        font-size: 15px;
        line-height: 160%;
        color: #CDCDCD;
        margin: 0;
        padding: 42px 0 0 0;
    }

    a {
        text-decoration: none;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 207px;
        height: 52px;
        background: #F1CDB3;
        border-radius: 100px;

        font-family: $georgia;
        font-style: normal;
        font-weight: normal;
        font-size: 17px;
        line-height: 130%;
        color: #292929;

        margin: 42px 0 0 0;
    }

    img {
        width: 260px;
        padding-top: 105px;
    }
}

@media (min-width: 768px) {
    .container {
        max-width: 768px;
        padding: 0 30px;
        margin: 0 auto;
    }

    .header {
        justify-content: space-between;
        align-content: center;

        img {
            display: none;
        }

        nav {
            display: block;
            width: 443px;

            ul {
                list-style-type: none;
                display: flex;
                justify-content: space-between;

                li {
                    a {
                        text-decoration: none;
                        font-family: $arial;
                        font-style: normal;
                        font-weight: normal;
                        font-size: 15px;
                        line-height: 160%;
                        color: #CDCDCD;
                        

                        &:focus{
                            color: #FAFAFA;
                            border-bottom: 3px solid #F1CDB3;
                        }
                        &:hover{
                            color: #FAFAFA;
                        }
                    }
                }
            }
        }
    }
    #first-elem {
        color: #FAFAFA;
        border-bottom: 3px solid #F1CDB3;
        &:hover{
            color: #F1CDB3;
        }
    }

    .not-only {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: start;
        text-align: start;
        padding-left: 139px;
    
        h2 {
            margin: 0;
            padding: 60px 0 0 0;
            font-size: 44px;
            line-height: 130%;
            max-width: 350px;
            text-align: left;
        }
    
        p {
            max-width: 460px;
            margin: 0;
        }
    
        img {
            width: 569px;
            padding-top: 100px;
            
        }

        a {
            margin-left: 126px;
        }
    }

}











/*-----------*/
.about-flex {
    display: flex;
    flex-direction: column;
    align-items: center;
    

    h2 {
        width: 293px;
        font-family: $georgia;
        font-style: normal;
        font-weight: normal;
        font-size: 25px;
        line-height: 130%;
        letter-spacing: 0.06em;
        color: #545454;
        text-align: center;
        margin: 0 0 0 0;
        padding: 42px 0 3px 0;
    }

    p {
        text-align: justify;
        font-family: Arial;
        font-style: normal;
        font-weight: normal;
        font-size: 15px;
        line-height: 160%;
        text-align: justify;
        color: #4C4C4C;

        margin: 0 0 0 0;
        padding: 22px 0 0 0;
    }

    img {
        padding: 42px 0;
        width: 260px;
    }
}


@media (min-width: 768px) {
    .about-flex {
        padding-left: 139px;
        align-items: start;
        width: 437px;
        
        
    
        h2 {
            width: 370px;
            font-size: 35px;
            text-align: left;
            margin: 0 0 0 0;
            padding: 80px 0 0px 0;
        }
    
        p {
            text-align: left;
            width: 430px;
            margin: 0 0 0 0;
            padding: 25px 0 0 0;
        }
    
        img {
            padding: 80px 0 100px 0;
            width: 300px;
            align-self: center;
        }
    }
}




/*-----------------*/
.our-friends {
    width: 100%;
    background: #F6F6F6;
    padding: 42px 0;
}

.of-flex {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    h2 {
        width: 300px;
        font-family: $georgia;
        font-style: normal;
        font-weight: normal;
        font-size: 25px;
        line-height: 130%;
        letter-spacing: 0.06em;
        color: #545454;
        text-align: center;
        margin: 0;
    }
}

.slider {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.left, .right {
    display: block;
    width: 52px;
    height: 52px;
    border-radius: 100px;
    border: 2px solid #F1CDB3;
    padding: 21px 0 0 16px;
    box-sizing: border-box;
    margin: 0 40px;
    
    &:hover {
        background: #FDDCC4;
        border: none;
        padding: 23px 0 0 0;
    }
}

.elem-friends {
    order: -1;
    width: 270px;
    text-align: center;
    background: #FAFAFA;
    border-radius: 9px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 34px 0 30px 0;
    
    margin: 42px 0 20px 0;


    p {
        font-family: Georgia;
        font-style: normal;
        font-weight: normal;
        font-size: 20px;
        line-height: 23px;
        letter-spacing: 0.06em;
        color: #545454;

        padding: 34px 0 0 0
    }

    a {
        text-decoration: none;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 187px;
        height: 52px;
        border: 2px solid #F1CDB3;
        border-radius: 100px;

        font-family: $georgia;
        font-style: normal;
        font-weight: normal;
        font-size: 17px;
        line-height: 130%;
        color: #292929;

        

    }
}

.button-friends {
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 260px;
    height: 52px;
    left: 20px;
    top: 655px;
    background: #F1CDB3;
    border-radius: 100px;

    font-family: $georgia;
    font-style: normal;
    font-weight: normal;
    font-size: 17px;
    line-height: 130%;
    letter-spacing: 0.06em;
    color: #292929;

    margin: 57px 0 0 0;
}


.help-flex {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 42px 0;

    h2 {
        margin: 0;
        padding: 0;
        text-align: center;
        font-family: Georgia;
        font-style: normal;
        font-weight: normal;
        font-size: 25px;
        line-height: 130%;
        letter-spacing: 0.06em;
        color: #545454;
    }
}

.help-solution {
    display: flex;
    flex-wrap: wrap;
    padding: 42px 0 0 0;

    .elem {
        width: 130px;
        margin: 0 10px;

        display: flex;
        flex-direction: column;
        align-items: center;
    }
}



.in-adition {
    background: #F6F6F6;
    width: 100%;
    padding: 42px 0;
}

.in-adition-flex {
    display: flex;
    flex-direction: column;
    align-items: center;

    h2 {
        margin: 0;
        padding: 0;

        font-family: $georgia;
        font-style: normal;
        font-weight: normal;
        font-size: 25px;
        line-height: 130%;
        letter-spacing: 0.06em;
        color: #545454;
        text-align: center;
    }

    p:first-of-type {
        font-family: $georgia;
        font-style: normal;
        font-weight: normal;
        font-size: 15px;
        line-height: 160%;
        color: #545454;
        padding: 20px 0 0 0;
        margin: 0;
    }

    p:last-of-type {
        width: 280px;
        font-family: Arial;
        font-style: italic;
        font-weight: normal;
        font-size: 12px;
        line-height: 18px;
        text-align: justify;
        color: #B2B2B2;
        padding: 20px 0 0 0;
        margin: 0;
    }

    .puppy {
        padding-top: 42px;
        margin: 0;
        width: 260px;
    }


    button {
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        padding: 0 0 0 0;
        margin: 20px 0 0 0;
        width: 281px;
        height: 43px;
        background: #F1CDB3;
        border-radius: 9px;
        border: none;

        font-family: $georgia;
        font-style: normal;
        font-weight: normal;
        font-size: 15px;
        line-height: 110%;
        letter-spacing: 0.06em;
        color: #545454;

        img {
            width: 30px;
        }
    }
}


footer {
